@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#override {
  font-family: 'Roboto Slab', serif;
}

.header-color {
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
}

.landing-body {
  background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%);
}

.resume-body{
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
}

.me-img {
  width: 25%;
  margin-top: 2em;
  border-radius: 20px;
}

.me2-img {
  width: 25%;
  margin-top: 2em;
  border-radius: 20px;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 85%;
  margin: auto;
  border-radius: 20px;
}

.banner-text h1 {
  font-family: 'Permanent Marker', cursive;
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px solid white;
  width: 75%;
  margin: auto;
}

.banner-text p {
  font-size: 20px;
  color: white;
  padding: 1em;
}

.header-color span{
  font-family: 'Permanent Marker', cursive;
}

header {
  font-family: 'Permanent Marker', cursive;
}

.nav-dr#nav-dr {
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  color: white;
  border-right: 0px;
}

.nav-dr#nav-dr span {
  font-family: 'Permanent Marker', cursive;
}

.nav-dr-nav#nav-dr-nav a {
  color: white;
}

.resume-render {
  width: 100%;
  height: 100%;
}

.resume-body h1, .contact-body h1, .projects-body h1, .aboutme-body h1 {
  margin: 0;
  font-size: 0;
}

@media only screen and (max-width: 1024px) {
  .resume-body h1, .aboutme-body h1, .projects-body h1, .contact-body h1 {
    margin: 0;
    font-size: 16px;
    padding-top: 17.8px;
    padding-bottom: 17.8px;
    color: white;
    font-family: 'Permanent Marker', cursive;
  }
  .mdl-layout__drawer-button i{
    color: white;
  }
}

.aboutme-body {
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.projects-body {
  background-image: linear-gradient(60deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
}

.contact-body {
  background-image: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
}

.aboutme-body, .projects-body, .contact-body, .landing-body {
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-bottom: 0;
  overflow: scroll;
}

.resume-body {
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-bottom: 0;
}

.content-layout{
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding-bottom: 0;
}

.aboutme-body h1, .projects-body h1, .contact-body h1 {
  margin: 0;
}

.video-wrap {
  margin-top: 2em;
  width: 70vw;
  height: 60vh;
  margin-bottom: 2em;
}

.contact-body svg {
  height: 10vh;
  margin-top: 2em;
}

.projects-body svg {
  height: 20vh;
}

.projects-body h2 {
  color: white;
  font-family: 'Permanent Marker', cursive;
  font-size: 25px;
  display: inline;
}

.projects-body p {
  color: white;
}

.email-wrap a {
  margin-top: 2em;
  color: white;
  font-size: 24px;
}

.aboutme-body p {
  width: 80%;
  margin: auto;
  margin-top: 2em;
  color: white;
  font-size: 20px;
  font-family: Roboto Slab;
}

.grid-container {
  width: 80%;
  margin: auto;
}

.mdl-layout {
  text-align: center;
}

.mdl-cell.mdl-cell--6-col {
  margin: auto;
}



